// ApiService.js
import axios from 'axios';

// // const API_BASE_URL = 'https://rate-calculator.asdorstaging.com';
// const API_BASE_URL = 'https://rate-calculator.asdordigital.com.sg';
const API_BASE_URL = process.env.VUE_APP_API_BASE_URL || 'https://rate-calculator.asdordigital.com.sg';

export const ApiService = {
  async getStatus() {
    try {
      const response = await axios.get(`${API_BASE_URL}/api`);
      return response.data;
    } catch (error) {
      console.error('Error fetching data:', error);
      throw error;
    }
  },
  async getSenderDetails(code, lang) {
    try {
      const response = await axios.get(`${API_BASE_URL}/api/sender?country_code=${code}&lang=${lang}`);
      return response.data;
    } catch (error) {
      console.error('Error fetching data:', error);
      throw error;
    }
  },
  async getReceiverList(code, lang) {
    try {
      const response = await axios.get(`${API_BASE_URL}/api/receiver?country_code=${code}&lang=${lang}`);
      return response.data;
    } catch (error) {
      console.error('Error fetching data:', error);
      throw error;
    }
  },
  async getPackageTypeList(code, lang) {
    try {
      const response = await axios.get(`${API_BASE_URL}/api/package-type?country_code=${code}&lang=${lang}`);
      return response.data;
    } catch (error) {
      console.error('Error fetching data:', error);
      throw error;
    }
  },
  async postCalculateRate(payload) {
    try {
      const response = await axios.post(`${API_BASE_URL}/api/calculate`, payload);
      return response.data;
    } catch (error) {
      console.error('Error fetching data:', error);
      throw error;
    }
  },
};
