<template>
    <div v-html="title"></div>
    <!-- STEP PROGRESS -->
    <div class="step-progress">
      <step-progress :lang="lang" :steps="mySteps" :current-step="currentStep" icon-class="bi bi-check" line-thickness="10" active-thickness="3" passive-thickness="3" passive-color="gray" active-color="green"></step-progress>
    </div>
    <br/>
    <!-- STEP 1 -->
    <div :class="`px-2 ${this.checkIfCountry('vn') && this.lang !== 'en' ? 'delivery-viet' : ''}`" v-if="currentStep == 1">
      <form class="needs-validation" id="myForm" onsubmit="return validateForm()">
        <!-- COUNTRY OR REGION FIELD -->
        <div class="row g-2 mb-2 text-start fw-bold d-none d-md-flex">
          <div class="col-md">
            <span>{{ $t('message.from') }}</span>
          </div>
          <div class="col-md">
            <span>{{ $t('message.to') }}</span>
          </div>
          <div class="col-md" v-if="(lang == 'en' && senderDetail.hide_package_opt_en) || (lang != 'en' && senderDetail.hide_package_opt_local)">
            <span>{{ $t('message.packageType') }}</span>
          </div>
        </div>
        <div class="row g-2 mb-2">
          <div class="col-md">
            <div class="text-start fw-bold d-md-none">
              <span>{{ $t('message.from') }}</span>
            </div>
            <div class="form-floating">
              <select class="form-select" id="floatingSelectGrid" aria-label="Floating label select example" disabled required>
                <option selected>{{ senderDetail.name }}</option>
              </select>
              <label for="floatingSelectGrid">{{ $t('message.countryOrRegion') }}</label>
              <div class="invalid-feedback text-start">
                {{ $t('message.errorCountry') }}
              </div>
            </div>
          </div>
          <div class="col-md">
            <div class="text-start fw-bold d-md-none">
              <span>{{ $t('message.to') }}</span>
            </div>
            <div class="form-floating">
              <select class="form-select" id="floatingSelectGrid" aria-label="Floating label select example" v-model="formCalculate.receiver_id" required>
                <option disabled value="" selected>{{ $t('message.countryOrRegion') }}</option>
                <option v-for="region in regionList" :key="region.id" :value="region.id">{{ region.name }}</option>
              </select>
              <label for="floatingSelectGrid">{{ $t('message.countryOrRegion') }}</label>
              <div class="invalid-feedback text-start">
                {{ $t('message.errorCountry') }}
              </div>
            </div>
          </div>
          <div class="col-md" v-if="(lang == 'en' && senderDetail.hide_package_opt_en) || (lang != 'en' && senderDetail.hide_package_opt_local)">
            <div class="text-start fw-bold d-md-none">
              <span>{{ $t('message.packageType') }}</span>
            </div>
            <div class="form-floating">
              <select class="form-select" aria-label="Floating label select example" v-model="packageItemList[0].packageTypeId" @change="changePackageType(packageItemList[0].packageTypeId, packageItemList[0].id)" required>
                <option disabled value="" selected>{{ $t('message.optionPackage') }}</option>
                <option v-for="packages in packageTypeList" :key="packages.id" :value="packages.id">{{ packages.name }}</option>
              </select>
              <label for="floatingSelectGrid">{{ $t('message.packageType') }}</label>
              <div class="invalid-feedback text-start">
                {{ $t('message.errorPackage') }}
              </div>
            </div>
          </div>
        </div>
        <!-- PACKAGE AND WEIGHT FIELD -->
        <template v-for="packageItem in packageItemList" :key="packageItem.id">
          <div class="row g-2 mb-4">
            <div class="col-md-6" v-if="(lang == 'en' && !senderDetail.hide_package_opt_en) || (lang != 'en' && !senderDetail.hide_package_opt_local)">
              <div class="text-start fw-bold">{{ $t('message.packageType') }}</div>
              <div class="form-floating">
                <select class="form-select" aria-label="Floating label select example" v-model="packageItem.packageTypeId" @change="changePackageType(packageItem.packageTypeId, packageItem.id)" required>
                  <option disabled value="" selected>{{ $t('message.optionPackage') }}</option>
                  <option v-for="packages in packageTypeList" :key="packages.id" :value="packages.id">{{ packages.name }}</option>
                </select>
                <label for="floatingSelectGrid">{{ $t('message.packageType') }}</label>
                <div class="invalid-feedback text-start">
                  {{ $t('message.errorPackage') }}
                </div>
              </div>
            </div>
            <div :class="(lang == 'en' && !senderDetail.hide_package_opt_en) || (lang != 'en' && !senderDetail.hide_package_opt_local) ? 'col-md-5' : 'col-md-12'">
              <div class="text-start fw-bold">{{ $t('message.weight') }}</div>
              <div class="form-floating">
                <div class="row" v-if="(lang == 'en' && senderDetail.hide_package_opt_en) || (lang != 'en' && senderDetail.hide_package_opt_local)">
                  <div class="col-sm-9 mb-3">
                    <vue-slider 
                      class="mt-3 p-3"
                      :min="0.5"
                      :marks="sliderMarks"
                      :disabled="packageItem.packageTypeId == 0 || packageItem.packageTypeId == null"
                      :max="maxSliderWeight(packageItem.packageTypeId)"
                      :interval="0.5"
                      v-model="packageItem.weightId" />
                  </div>
                  <div class="col-sm-3 mb-3 align-items-center justify-content-center" :key="`${packageItem.id}_${maxSliderWeight(packageItem.packageTypeId)}`">
                    <div class="form-floating">
                      <input type="number" class="form-control" v-model="packageItem.weightId" :min="0.5" :max="maxSliderWeight(packageItem.packageTypeId)" step="0.5" required :disabled="packageItem.packageTypeId == 0 || packageItem.packageTypeId == null">
                      <label for="floatingSelectGrid">{{ $t('message.weight') }}</label>
                    </div>
                  </div>
                </div>
                <select class="form-select" aria-label="Floating label select example" v-model="packageItem.weightId" required :disabled="packageItem.packageTypeId == 0 || packageItem.packageTypeId == null" v-if="(lang == 'en' && !senderDetail.hide_package_opt_en) || (lang != 'en' && !senderDetail.hide_package_opt_local)">
                  <option disabled value="" selected>{{ $t('message.optionWeight') }}</option>
                  <option v-for="weight in filteredWeightList(packageItem.packageTypeId)" :key="weight.id" :value="weight.value">{{ weight.value }}</option>
                </select>
                <label for="floatingSelectGrid" v-if="(lang == 'en' && !senderDetail.hide_package_opt_en) || (lang != 'en' && !senderDetail.hide_package_opt_local)">{{ $t('message.weight') }}</label>
                <div class="invalid-feedback text-start">
                  {{ $t('message.errorWeight') }}
                </div>
              </div>
            </div>
            <div class="col-md-1 d-flex justify-content-center align-items-center" v-if="(lang == 'en' && !senderDetail.hide_package_opt_en) || (lang != 'en' && !senderDetail.hide_package_opt_local)">
              <button type="button" class="btn btn-danger" :disabled="packageItemList.length === 1" @click="removeAnotherPackage(packageItem.id)"><i class="bi bi-trash3-fill"></i></button>
            </div>
          </div>
        </template>
  
        <!-- ALERT IF PACKAGE MORE THAN 3 (pcs)-->
        <div v-if="showAlert" class="alert alert-danger d-flex align-items-center justify-content-center alert-dismissible fade show" role="alert">
          <i class="bi bi-exclamation-triangle-fill me-2"></i>
          <div>
            {{ $t('message.errorMaxAddPackage') }}
          </div>
          <button @click="hideAlert" type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
        </div>

        <!-- BUTTON ADD MORE PACKAGE AND WEIGTH -->
        <div class="d-flex justify-content-end my-5" v-if="(lang == 'en' && !senderDetail.hide_package_opt_en) || (lang != 'en' && !senderDetail.hide_package_opt_local)">
          <button type="button" class="btn btn-danger fw-bold me-2 btn-text-size" @click="addAnotherPackage()" :disabled="packageItemList.length == 3"><i class="bi bi-plus-square-fill me-2"></i>{{ $t('message.btnAddMorePackage') }}</button>
        </div>

        <!-- BUTTON CALCULATE SHIPPING COST -->
        <div>
          <button type="button" class="btn btn-danger w-100 fw-bold btn-text-size" @click="postCalculateCost()" :disabled="isLoading">
            <template v-if="isLoading">
              <div class="d-flex justify-content-center">
                <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                <!-- Loading... -->
              </div>
            </template>
            <template v-else>
              {{ $t('message.btnCalculateShippingCost') }}
            </template>
          </button>
        </div>
        <!-- TEXT NOTE CALCULATION -->
        <div class="text-muted mt-2" v-if="hideStep1Footer() || checkIfCountry('jp')">
          <div class="row">
            <div class="col-md text-start fs-11" v-if="!checkIfCountry('jp')">
              <span>{{ $t('message.noteCalculation_1') }}</span> <br>
              <span>{{ $t('message.noteCalculation_2') }}</span>
              <div v-if="checkIfCountry('tw')" v-html="$t('message.noteCalculation_tw')"></div>
            </div>
            <div class="col-md text-start fs-11" v-if="checkIfCountry('jp')">
              <span>{{ $t('message.noteCalculation_jp_1') }}</span> <br>
              <span>{{ $t('message.noteCalculation_jp_2') }}</span> <br>
              <span>{{ $t('message.noteCalculation_jp_3') }}</span> <br>
              <span>{{ $t('message.noteCalculation_jp_4') }}</span> <br>
              <span>{{ $t('message.noteCalculation_jp_5') }}</span> <br>
              <span>{{ $t('message.noteCalculation_jp_6') }}</span> <br>
              <span ><div class="" v-html="$t('message.noteCalculation_jp_7')"></div></span>
            </div>
          </div>
        </div>
        <!-- COUNTRIES AND TERRITORIES HIDE FOR KR AND JP -->
        <div class="text-muted mt-2" v-if="hideStep1Footer() && !checkIfCountry('jp')">
          <div class="row mb-1">
            <div class="col-md-2 col-lg-1 text-start fs-11">
              <span v-html="$t('message.cn_1')"></span>
            </div>
            <div class="col-md-6 text-start fs-11">
              {{$t('message.cn_1_content')}}
            </div>
          </div>
          <div class="row mb-1">
            <div class="col-md-2 col-lg-1 text-start fs-11">
              <span v-html="$t('message.cn_2')"></span>
            </div>
            <div class="col-md-6 text-start fs-11">
              {{$t('message.cn_2_content')}}
            </div>
          </div>
          <div class="row mb-1">
            <div class="col-md-2 col-lg-1 text-start fs-11">
              <span v-html="$t('message.usa_1')"></span>
            </div>
            <div class="col-md-6 text-start fs-11">
              {{$t('message.usa_1_content')}}
            </div>
          </div>
          <div class="row mb-1">
            <div class="col-md-2 col-lg-1 text-start fs-11">
              <span v-html="$t('message.usa_2')"></span>
            </div>
            <div class="col-md-6 text-start fs-11">
              {{$t('message.usa_2_content')}}
            </div>
          </div>
        </div>
      </form>
    </div>

    <!-- STEP 2 -->
    <div :class="`px-2 ${this.checkIfCountry('vn') && this.lang !== 'en' ? 'delivery-viet' : ''}`" v-if="currentStep == 2">
      <div class="card shadow mb-3">
        <div class="card-body mt-1">
          <div class="row">
            <div class="col-md-2 d-flex align-items-center justify-content-center">
              <img :src="require('@/assets/images/business.jpeg')" width="250" height="150" class="img-fluid">
            </div>
            <div class="col-md-6 d-flex align-items-center">
              <div class="row row-cols-1 text-start text-decoration-none">
                <div class="col title fw-bold mb-3">
                  <div v-html="cardTitle(true)">
                  </div>
                </div>
                <div class="col content" @click="onClickContent">
                  <div v-html="cardContent(true)">
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="row mb-2 px-1">
                <div class="col fw-bold d-flex text-start align-items-center justify-content-start">
                  <span>{{ $t('message.freightCharge') }}</span>
                </div>
                <div class="col">
                  <span class="text-decoration-line-through small">{{ data.currency_code + " " + data.rates.original }}</span> <br>
                  <span class="fw-bold">{{ data.currency_code + " " + data.rates.business }}</span>
                </div>
              </div>
              <div v-for="item in additionalList(true)" :key="item.name">
                <div class="row mb-2 fw-bold px-1">
                  <div class="col d-flex text-start align-items-center justify-content-start">
                    <span>{{ item.name }}</span>
                  </div>
                  <div class="col">
                    <span>{{ item.value }}</span>
                  </div>
                </div>
              </div>
              <div class="row fw-bold mb-2">
                <div class="col p-0">
                  <a @click="navigateLink('business.cta_btn_link_en', data.langs.business.cta_btn_link_en)" rel="noopener noreferrer" v-if="lang == 'en'">
                    <button type="button" class="btn btn-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.business.cta_btn_text_en : data.langs.business.cta_btn_text_local }}</button>
                  </a>
                  <a @click="navigateLink('business.cta_btn_link_local', data.langs.business.cta_btn_link_local)" rel="noopener noreferrer" v-else>
                    <button type="button" class="btn btn-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.business.cta_btn_text_en : data.langs.business.cta_btn_text_local }}</button>
                  </a>
                </div>
              </div>
              <div class="row fw-bold" v-if="(lang == 'en' && data.langs.business.cta_btn_link_2_en) || (lang != 'en' && data.langs.business.cta_btn_link_2_local)">
                <div class="col p-0">
                  <a @click="navigateLink('business.cta_btn_link_2_en', data.langs.business.cta_btn_link_2_en)" rel="noopener noreferrer" v-if="lang == 'en'">
                    <button type="button" class="btn btn-outline-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.business.cta_btn_text_2_en : data.langs.business.cta_btn_text_2_local }}</button>
                  </a>
                  <a @click="navigateLink('business.cta_btn_link_2_local', data.langs.business.cta_btn_link_2_local)" rel="noopener noreferrer" v-else>
                    <button type="button" class="btn btn-outline-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.business.cta_btn_text_2_en : data.langs.business.cta_btn_text_2_local }}</button>
                  </a>
                </div>
              </div>
              <div v-for="item in conditionList(true)" :key="item">
                <div class="row mt-2 text-muted text-start" style="font-size: 12px;">
                  <div class="col ms-3 p-0">
                    <span>{{ item }}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="card shadow" v-if="!this.isHideIndia">
        <div class="card-body">
          <div class="row">
            <div class="col-md-2 d-flex align-items-center justify-content-center">
              <img :src="require('@/assets/images/personal.jpeg')" width="250" height="150" class="img-fluid">
            </div>
            <div class="col-md-6 d-flex align-items-center">
              <div class="row row-cols-1 text-start text-decoration-none">
                <div class="col title fw-bold mb-3">
                  <div v-html="cardTitle(false)">
                  </div>
                </div>
                <div class="col content" @click="onClickContent">
                  <div v-html="cardContent(false)">
                  </div>
                </div>
              </div>
            </div>
            <div class="col-md-4">
              <div class="row mb-2 px-1">
                <div class="col fw-bold d-flex text-start align-items-center justify-content-start">
                  <span>{{ $t('message.freightCharge') }}</span>
                </div>
                <div class="col">
                  <span class="text-decoration-line-through small">{{ data.currency_code + " " + data.rates.original }}</span> <br>
                  <span class="fw-bold">{{ data.currency_code + " " + data.rates.personal }}</span>
                </div>
              </div>
              <div v-for="item in additionalList(false)" :key="item.name">
                <div class="row mb-2 fw-bold px-1">
                  <div class="col d-flex text-start align-items-center justify-content-start">
                    <span>{{ item.name }}</span>
                  </div>
                  <div class="col">
                    <span>{{ item.value }}</span>
                  </div>
                </div>
              </div>
              <div class="row fw-bold mb-2">
                <div class="col p-0">
                  <a @click="navigateLink('personal.cta_btn_link_en', data.langs.personal.cta_btn_link_en)" rel="noopener noreferrer" v-if="lang == 'en'">
                    <button type="button" class="btn btn-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.personal.cta_btn_text_en : data.langs.personal.cta_btn_text_local }}</button>
                  </a>
                  <a @click="navigateLink('personal.cta_btn_link_local', data.langs.personal.cta_btn_link_local)" rel="noopener noreferrer" v-else>
                    <button type="button" class="btn btn-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.personal.cta_btn_text_en : data.langs.personal.cta_btn_text_local }}</button>
                  </a>
                </div>
              </div>
              <div class="row fw-bold" v-if="(lang == 'en' && data.langs.personal.cta_btn_2_link_en) || (lang != 'en' && data.langs.personal.cta_btn_2_link_local)">
                <div class="col p-0">
                  <a @click="navigateLink('personal.cta_btn_2_link_en', data.langs.personal.cta_btn_2_link_en)" rel="noopener noreferrer" v-if="lang == 'en' && data.langs.personal.cta_btn_2_link_en">
                    <button type="button" class="btn btn-outline-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.personal.cta_btn_text_2_en : data.langs.personal.cta_btn_text_2_local }}</button>
                  </a>
                  <a @click="navigateLink('personal.cta_btn_2_link_local', data.langs.personal.cta_btn_2_link_local)" rel="noopener noreferrer" v-else>
                    <button type="button" class="btn btn-outline-danger col-11 fw-bold btn-text-size">{{ $route.path == '/en' ? data.langs.personal.cta_btn_text_2_en : data.langs.personal.cta_btn_text_2_local }}</button>
                  </a>
                </div>
              </div>
              <div v-for="item in conditionList()" :key="item">
                <div class="row mt-2 text-muted text-start" style="font-size: 12px;">
                  <div class="col ms-3 p-0">
                    <span>{{ item }}</span>
                  </div>
                </div>
              </div>
              <div class="col px-1 content" v-if="cardCaption(false)">
                <div class="text-start caption" v-html="cardCaption(false)">
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- TEXT NOTE STEP 2 -->
      <div class="text-muted mt-2">
        <div class="text-start" style="font-size: 12px;">
          <div v-html="$route.path == '/en' ? data.langs.footer.en : data.langs.footer.local"></div>
        </div>
      </div>
      <!-- BUTTON BACK -->
      <div class="text-start">
        <button class="btn btn-danger fw-bold btn-text-size" @click="currentStep = 1">{{ $t('message.btnBack') }}</button>
      </div>
    </div>
  </template>
  
  <script>
  import StepProgress from 'vue-step-progress';
  import { ApiService } from '../services/ApiService.js';
  import VueSlider from 'vue-slider-component'
  import 'vue-slider-component/theme/antd.css'
  
  export default {
    name: 'MyHome',
    components: {
      'step-progress': StepProgress,
      'vue-slider': VueSlider
    },
    data() {
      return {
        mySteps: [ this.$t('message.myStep_1'), this.$t('message.myStep_2'), this.$t('message.myStep_3')],
        currentStep: 1,
        data: {},
        isLoading: false,
        senderDetail: {
          doc_max_weight: 2,
          non_doc_max_weight: 2,
          hide_package_opt_local: false,
          hide_package_opt_en: false,
          hide_step_1: false,
        },
        countryList: [],
        regionList: [],
        packageTypeList: [],
        weightList: [
          {
            id: 1,
            type: 1,
            data: []
          },
          {
            id: 2,
            type: 2,
            data: []
          }
        ],
        packageItemList: [
          {
            id: null,
            packageTypeId: null,
            weightId: 0.5
          }
        ],
        formCalculate: {
          country_code: "",
          receiver_id: 0,
          promo_code: "",
          promo_discount: "",
          packages: []
        },
        showAlert: false,
        promo_code: '',
        promo_code_2: '',
        isCopied: false,
        lang: '',
        weight: 0,
        sliderMarks: [0.5],
        country_code: "",
        type: "",
        isHideIndia: false
      }
    },
    beforeCreate() {
      let lang = "";
      
      if (this.$route.path.slice(1).toString() == 'en') {
        lang = 'en'
      } else if (this.$route.query.country_code) {
        lang = this.$route.query.country_code.toLocaleLowerCase();
      } else {
        lang = this.$route.query.countryCode.toLocaleLowerCase();
      }

      this.$i18n.locale = lang;
      this.lang = lang;
      console.log(this.$i18n.locale)
      console.log('lang', this.lang)
    },
    created() {
    },
    mounted() {
      this.init();

      if(this.$route.query.promo_code !== undefined) {
        this.promo_code = this.$route.query.promo_code;
      }

      if(this.$route.query.promo_code_2 !== undefined) {
        this.promo_code_2 = this.$route.query.promo_code_2;
      }

      this.lang = this.$i18n.locale;

      if(this.lang != 'en' && this.checkIfCountry('vn')) {
        const appDivs = document.querySelectorAll('div#app');
        appDivs.forEach(div => {
          div.classList.add('delivery-viet');
        });
      }
    },
    computed: {
      title() {
        return this.currentStep == 1 ? this.$t('message.currentStep_1') : this.$t('message.currentStep_2') ;
      },
      filteredWeightList() {
        return (packageTypeId) => {
          // Implement this method to filter the weightList based on the selected package type
          // You can use this.weightList and packageTypeId to return the filtered weight options
          if (packageTypeId == 1) {
            return this.weightList[0].data
          } else {
            return this.weightList[1].data
          }
        };
      },
      cardTitle() {
        return (isBusiness) => {
          if (isBusiness) {
            return this.$route.path == '/en' ? this.data.langs.business.title_en : this.data.langs.business.title_local;
          } else {
            return this.$route.path == '/en' ? this.data.langs.personal.title_en : this.data.langs.personal.title_local;
          }
        }
      },
      cardContent() {
        return (isBusiness) => {
          if (isBusiness) {
            let content = this.$route.path == '/en' ? this.data.langs.business.content_en : this.data.langs.business.content_local;

            if(this.promo_code == '') {
              return content;
            }

            let formattedContent = content.replace(
              new RegExp(this.promo_code, 'g'),
              `<span class="copy-to-clipboard fw-bold ${this.isCopied ? 'copied' : ''}" title="Copy to clipboard">${this.promo_code}</span>`
            );

            if(this.promo_code_2 == ''){
              return formattedContent;
            }

            let formattedContent_2 = formattedContent.replace(
              new RegExp(this.promo_code_2, 'g'),
              `<span class="copy-to-clipboard fw-bold ${this.isCopied ? 'copied' : ''}" title="Copy to clipboard">${this.promo_code_2}</span>`
            );

            return formattedContent_2

          } else {
            let content = this.$route.path == '/en' ? this.data.langs.personal.content_en : this.data.langs.personal.content_local;

            if(this.promo_code == '') {
              return content;
            }
            
            let formattedContent = content.replace(
              new RegExp(this.promo_code, 'g'),
              `<span class="copy-to-clipboard fw-bold ${this.isCopied ? 'copied' : ''}" title="Copy to clipboard">${this.promo_code}</span>`
            );

            if(this.promo_code_2 == ''){
              return formattedContent;
            }

            let formattedContent_2 = formattedContent.replace(
              new RegExp(this.promo_code_2, 'g'),
              `<span class="copy-to-clipboard fw-bold ${this.isCopied ? 'copied' : ''}" title="Copy to clipboard">${this.promo_code_2}</span>`
            );

            return formattedContent_2;
          }
        }
      },
      cardCaption() {
        return (isBusiness) => {
          if (isBusiness) {
            return this.$route.path == '/en' ? this.data.langs.business.caption_en : this.data.langs.business.caption_local;
          } else {
            return this.$route.path == '/en' ? this.data.langs.personal.caption_en : this.data.langs.personal.caption_local;
          }
        }
      },
      additionalList() {
        return (isBusiness) => {
          if (isBusiness) {
            return this.$route.path == '/en' ? this.data.langs.business.additional_list_en : this.data.langs.business.additional_list_local;
          } else {
            return this.$route.path == '/en' ? this.data.langs.personal.additional_list_en : this.data.langs.personal.additional_list_local
          }
        }
      },
      conditionList() {
        return (isBusiness) => {
          if (isBusiness) {
            return this.$route.path == '/en' ? this.data.langs.business.condition_list_en : this.data.langs.business.condition_list_local;
          } else {
            return this.$route.path == '/en' ? this.data.langs.personal.condition_list_en : this.data.langs.personal.condition_list_local
          }
        }
      }
    },
    methods: {
      checkIfCountry(country_code){
        return this.country_code.toLowerCase() == country_code
      },
      minSliderWeight() {
        return 0.5
      },
      hideStep1Footer(){
        return this.country_code.toLowerCase() != 'kra' && this.country_code.toLowerCase() != 'krb' && this.country_code.toLowerCase() != 'in'
      },
      maxSliderWeight(packageTypeId){
        var max = 0;

        if (packageTypeId == 1) {
          max = parseFloat(this.senderDetail.doc_max_weight)
        } else {
          max = parseFloat(this.senderDetail.non_doc_max_weight)
        }

        // this.sliderMarks = [0.5, max]
        // console.log('max', max)
        // console.log(this.sliderMarks)
        
        return max
      },
      onClickContent(event) {
        if (event.target.classList.contains('copy-to-clipboard')) {
          console.log('copy')
          const el = document.createElement('textarea');
          el.value = this.promo_code;
          document.body.appendChild(el);
          el.select();
          document.execCommand('copy');
          document.body.removeChild(el);

          this.isCopied = true;

          setTimeout(() => {
            this.isCopied = false;
          }, 500)
        }
      },
      async init() {
        let code = '';
        let lang = this.$route.path
        
        if (this.$route.query.country_code) {
          code = this.$route.query.country_code.toLocaleLowerCase();
        } else {
          code = this.$route.query.countryCode.toLocaleLowerCase();
        }
        
        this.country_code = code;

        if (this.$route.query.type) {
          this.type = this.$route.query.type.toLocaleLowerCase();
        }

        await this.loadSenderDetail(code, lang);
        await this.loadReceiverList(code, lang);
        await this.loadPackageTypeList(code, lang);
        await this.loadWeightlist();
      },

      async loadSenderDetail(code, lang) {
        try {
          this.isLoading = true;

          let data = await ApiService.getSenderDetails(code, lang);
          this.senderDetail = data.data;

          if(this.senderDetail.hide_step_1){
            this.mySteps = [this.$t('message.myStep_2'), this.$t('message.myStep_3')]
          }

          this.isLoading = false;

        } catch (error) {
          console.log(error);
          this.isLoading = false;
        }
      },
      async loadReceiverList(code, lang) {
        try {
          this.isLoading = true;

          let data = await ApiService.getReceiverList(code, lang);
          this.regionList = data.data;
          this.isLoading = false;
        } catch (error) {
          console.log(error);
          this.isLoading = false;
        }
      },
      async loadPackageTypeList(code, lang) {
        try {
          this.isLoading = true;

          let data = await ApiService.getPackageTypeList(code, lang);
          console.log(code, lang);
          if (code === 'kor' && lang === '/local') {
            this.packageTypeList = [
              {
                id: 1,
                name: '문서'
              },
              {
                id: 2,
                name: '비문서'
              }
            ];
          } else {
            this.packageTypeList = [
              {
                id: 1,
                name: data.data[1]
              },
              {
                id: 2,
                name: data.data[2]
              }
            ];
          }
          this.isLoading = false;
        } catch (error) {
          console.log(error);
          this.isLoading = false;
        }
      },
      async loadWeightlist() {
        for (let i = 0; i < this.weightList.length; i++) {       
          if (this.weightList[i].type === 1) {          
            for (let j = 1; j <= this.senderDetail.doc_max_weight * 2; j++) {
              this.weightList[i].data.push({
                id: j,
                value: j * 0.5
              })
            }
          } else {
            for (let k = 1; k <= this.senderDetail.non_doc_max_weight * 2; k++) {
              this.weightList[i].data.push({
                id: k,
                value: k * 0.5
              })
            }
          }
        }
      },
      async postCalculateCost() {
        try {
          this.isLoading = true;
          await this.validateForm();

          let payload = {};
          if (this.$route.query.country_code) {
            payload.country_code  = this.$route.query.country_code;
          } else {
            payload.country_code  = this.$route.query.countryCode;
          }
          payload.receiver_id = this.formCalculate.receiver_id;
          payload.promo_code = this.promo_code;
          payload.promo_discount = this.$route.query.promo_discount;
          payload.promo_code_2 = this.promo_code_2
          payload.packages = [];

          for (let i = 0; i < this.packageItemList.length; i++) {
            payload.packages.push(
              {
                type: this.packageItemList[i].packageTypeId,
                weight: this.packageItemList[i].weightId
              }
            )
          }

          let data = await ApiService.postCalculateRate(payload);

          this.data = data.data;
          this.currentStep = 2;

          if(this.country_code == 'th'){
            this.$gtag.event('cal_ship', {
              event_category: 'cal_ship',
              event_label: 'click'
            });

            console.log('executed cs')
          }

          // manually handle country with type organic
          if ((this.country_code == 'th') && this.type != '' && this.type == 'organic') {
            this.data.langs.business.cta_btn_link_en = 'https://www.dhl.com/discover/en-th/sme360-dbo-organic/calculate-now/business-account';
            this.data.langs.business.cta_btn_link_local = 'https://www.dhl.com/discover/th-th/sme360-dbo-organic/calculate-now/business-account'
          } else if ((this.country_code == 'vn') && this.type != '' && this.type == 'organic') {
            this.data.langs.business.cta_btn_link_en = 'https://www.dhl.com/discover/en-vn/sme360-dbo-organic';
            this.data.langs.business.cta_btn_link_local = 'https://www.dhl.com/discover/vi-vn/sme360-dbo-organic'
          } 
          
          // manually handle country india with tracker pixel and gtag
          if (this.country_code == 'in') {
            // Facebook Pixel - Track custom event
            // eslint-disable-next-line no-undef
            fbq('track', 'CalculateShippingCost');

            // Google Conversion Tracking
            // eslint-disable-next-line no-undef
            gtag('event', 'conversion', {
                'send_to': '826354192/7sDnCJOo59oZEJDUhIoD'
            });
          }

          // check if country HK will hide content for india
          if ((this.country_code == 'hkg' || this.country_code == 'HKG' )&& payload.receiver_id == '4229') {
            this.isHideIndia = true;
          }

          this.isLoading = false;
        } catch (error) {
          console.log(error);
          this.isLoading = false;
        }
      },
      navigateLink(key, link) {
        if(key == 'personal.cta_btn_link_en' || key == 'personal.cta_btn_link_local') {
          
          if(this.country_code == 'th'){
            this.$gtag.event('contact_chat', {
              event_category: 'contact_chat',
              event_label: 'click'
            });
            console.log('executed cc')
          }
          
        } else if(key == 'personal.cta_btn_2_link_en' || key == 'personal.cta_btn_2_link_local') {
          
          if(this.country_code == 'th'){
            this.$gtag.event('mydhl+_ship', {
              event_category: 'mydhl+_ship',
              event_label: 'click'
            });
            console.log('executed ms')
          }

        } else if(key == 'business.cta_btn_link_en' || key == 'business.cta_btn_link_local') {
          if(this.country_code == 'th'){
            this.$gtag.event('quote_business_account', {
              event_category: 'quote_business_account',
              event_label: 'click'
            });
            console.log('executed qba')
          }
        }

        window.open(link, '_blank');
      },
      validateForm() {
        let form = document.getElementById('myForm');
        if (form.checkValidity() === false) {
          event.preventDefault();
          event.stopPropagation();
        }
        form.classList.add('was-validated');
        return form.checkValidity();
      },

      addAnotherPackage() {
        console.log(this.packageItemList.length)
        if (this.packageItemList.length < 3) {
          this.packageItemList.push(
            {
              id: this.packageItemList.length + 1,
              packageTypeId: null,
              weightId: 0.5
            }
          )
        } else {
          //alert for bootstrap 5
          this.showAlert = true;
        }
      },
      removeAnotherPackage(id) {
        console.log(id)

        // Use the splice method to remove the object with the specified ID from the original array
        for (let i = 0; i < this.packageItemList.length; i++) {
          if (this.packageItemList[i].id === id) {
            this.packageItemList.splice(i, 1);
            break; // Stop the loop once the object is removed
          }
        }

        this.showAlert = false;
      },
      hideAlert() {
        // Hide the alert when the close button is clicked
        this.showAlert = false;
      },

      changePackageType(packageTypeId, packageId) {
        var max = 0

        if (packageTypeId == 1) {
          max = parseFloat(this.senderDetail.doc_max_weight)
        } else {
          max = parseFloat(this.senderDetail.non_doc_max_weight)
        }

        this.sliderMarks = [0.5, max]

        // return 

        // console.log(id)
        var index = this.packageItemList.findIndex(x => x.id === packageId);
        this.packageItemList[index].packageTypeId = packageTypeId;
        this.packageItemList[index].weightId = 0.5;

        // for (let i = 0; i < this.packageItemList.length; i++) {
        //   if (this.packageItemList[i].id === id) {
        //     this.packageItemList[i].packageTypeId = id;
        //     this.packageItemList[i].weightId = 0.5;
        //     break; // Stop the loop once the object is found
        //   }
        // }
      },
    }
  }
  </script>
