import VueGtag from "vue-gtag";

import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap-icons/font/bootstrap-icons.css';
import 'vue-step-progress/dist/main.css';

const app = createApp(App);

app.use(router);
