// router/index.js
import { createRouter, createWebHistory } from 'vue-router';
import MyHome from '../views/MyHome.vue';

const routes = [
  {
    path: '/en',
    component: MyHome,
  },
  {
    path: '/local',
    component: MyHome,
  },
  // Catch-all route
  {
    path: '/:catchAll(.*)',
    redirect: to => {
      // Redirect to '/en' with preserved query parameters
      return {
        path: '/local',
        query: {
          country_code: to.query.country_code, 
          promo_code: to.query.promo_code, 
          promo_discount: to.query.promo_discount, 
          countryCode: to.query.countryCode,
          type: to.query.type
        }
      };
    }
  },
  // Add more routes as needed
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
